.b-burger {
  display: none;
  border: none;
  background: transparent;
  box-shadow: none;
  width: 32px;
  height: 20px;
  position: relative;
  transform: rotate(0);
  transition: .5s ease-in-out;
  cursor: pointer;
}

.b-burger span {
  display: block;
  position: absolute;
  height: 4px;
  width: 100%;
  border-radius: 9px;
  transform: rotate(0);
  transition: .25s ease-in-out;
  opacity: 1;
  left: 0;
}

.b-burger span:nth-child(1) {
  top: 0;
  transform-origin: center center;
}

.b-burger span:nth-child(2) {
  top: 50%;
  transform-origin: left center;
}

.b-burger span:nth-child(3) {
  top: 100%;
  transform-origin: center center;
}

.b-burger--active span:nth-child(1) {
  transform: translate(0, -50%) rotate(45deg);
  top: 50%;
}

.b-burger--active span:nth-child(2) {
  width: 0;
  opacity: 0;
}

.b-burger--active span:nth-child(3) {
  transform: translate(0, -50%) rotate(-45deg);
  top: 50%;
}

.b-button {
  display: flex;
  align-items: center;
  justify-content: space-around;
  border-radius: 50px;
  width: 300px;
  height: 60px;
  transition: all .3s;
  border: none;
  box-shadow: none;
  background: white;
}

.b-button__content {
  color: #000;
  font-size: 1rem;
}

@media (max-width: 768px) {
  .b-button__content {
    font-size: .7rem;
  }
}

.page-top {
  width: 100%;
  height: 40px;
  background-color: #95C11F;
}

.page-top .container {
  display: flex;
  align-items: center;
  height: 100%;
}

.page-top__search {
  margin-left: auto;
}

.page-top__chevron {
  display: flex;
  align-items: center;
}

.page-top__chevron img {
  margin-right: 9px;
}

.page-top__chevron span {
  font-size: 14px;
  line-height: 25px;
  color: #FFFFFF;
  text-transform: uppercase;
}

.page-top__search {
  display: flex;
  align-items: center;
  margin-right: 10px;
}

.page-top__search .open-search {
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  margin-right: 10px;
}

.page-top__search .open-search:hover {
  opacity: 0.7;
}

.page-top__search .input-search {
  width: 0;
  opacity: 0;
  transition: all .3s ease-out;
  border: none;
  background: #fff;
}

.page-top__search .input-search--visible {
  opacity: 1;
  width: 200px;
  border: 1px solid #95C11F;
  border-radius: 4px;
  height: 30px;
  padding-left: 15px;
}

.page-top__langs {
  display: flex;
  align-items: center;
}

.page-top__langs .ru {
  padding-right: 10px;
  position: relative;
}

.page-top__langs .ru:after {
  content: "";
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  position: absolute;
  border-right: 1px solid #fff;
}

.page-top__langs .eng {
  padding-left: 10px;
}

.page-top__langs .lang-btn {
  font-size: 14px;
  line-height: 25px;
  color: #FFFFFF;
  opacity: 0.7;
  font-family: "Open Sans", sans-serif;
  font-weight: 300;
  text-transform: uppercase;
  color: #fff;
}

.page-top__langs .lang-btn--active {
  opacity: 1;
  font-weight: 600;
  color: #fff;
}

